.Card_structure {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 0;
        margin: 0;
        gap: 50px 60px;
}

.Card_aspect {
    list-style-type: none;
    width: 340px;
    height: 340px;
    position: relative;
    border-radius: 10px; 
    
        &_color {
            background : linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 10px;
        }   
        &_img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 10px;
        }
        &_title {
            font-size: 18px;
            font-weight: 500;
            color: white;            
            position: absolute;
            left: 5.88%;
            right: 5.88%;
            top: 78.82%;
            bottom: 5.88%;
        }               
}
        
    
@media screen and (max-width: 795px){
.Card_structure {
    gap: 20px 60px;
}
.Card_aspect {
    width: 100%;
    height: 255px;
            
        &_title {
            left: 5.88%;
            right: 5.88%;
            top : 72%;
            bottom: 5.88%;
            }
        }
    }
       
    
  